<template>
  <div class="convertRecord">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>提现记录</span>
    </header>
    <div class="ballist">
      <div class="ballist-first">
        <div class="bal-box zoomBor1" v-for="(item, index) in data" :key="index">
          <div class="ballist-top">
            <div v-if="item.type == 1">支付宝提现</div>
            <div v-if="item.type == 2">银行卡提现</div>
            <div>￥{{item.money}}</div>
          </div>
          <div class="ballist-bottom textyin">
            <div>{{item.create_time | timeFilter}}</div>
            <div v-if="item.state == 0">未处理</div>
            <div v-if="item.state == 1">成功</div>
            <div v-if="item.state == 2">失败</div>
          </div>
        </div>
      </div>
      <div class="ballist-bot">
        没有更多了
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        data:[]
      }
    },
    filters:{
      timeFilter(val){
        return new Date(val*1000).toLocaleDateString();
      }
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      record(page=1){
        this.post('home/Withdraw/withdrowLog',{
          page: page,
          number:20
        }).then(res => {
          if (res.data.code == 10000){
              this.data = res.data.msg.data;
          }
        })
      }
    },
    mounted(){
      this.record();
    }
  }
</script>
<style scoped>
  @import "../../../static/header.css";
  @import "../../../static/index.css";
  .convertRecord{
    height: 100vh;
    background-color: #f2f2f2;
  }
  .ballist{
    height: calc(100%-0.88rem);
    overflow-y: auto;
  }
  .ballist .zoomBor1:after{
    width:calc(100% + .2rem);
    left:0;
  }
  .ballist-bot{
    margin-top:.5rem;
    color: #ccc;
    text-align: center;
  }
</style>



